import React from "react";
import {
  ProductCardLayout,
  TruePrice,
  PriceContainer,
  TestDiv,
  DetailsImageContainer,
} from "./../../styles/product-card";
import { connect } from "react-redux";

function ProductCard(props) {
  const product = props.product;
  const getCurrencySymbol = () => {
    if (props.currency.code === "EUR") {
      return <span>&euro;</span>;
    }
    if (props.currency.code === "USD") {
      return <span>&#36;</span>;
    }
  };
  // {/* <div className="image-container"> */}
  return (
    <ProductCardLayout>
      {product.sale ? (
        <div className="image-container">
          <img src={product.image} alt="product" />
          <p>Sale</p>
        </div>
      ) : (
        <img src={product.image} alt="product" />
      )}
      <h3>{product.name}</h3>

      <PriceContainer>
        {product.sale && (
          <p className="old-price">
            {getCurrencySymbol()}
            {props.numberToCurrency(props.currency.price * product.oldPrice)}
          </p>
        )}
        <TruePrice>
          {getCurrencySymbol()}
          {props.numberToCurrency(props.currency.price * product.price)}
        </TruePrice>
      </PriceContainer>
    </ProductCardLayout>
  );
}

const mapStateToProps = (state) => {
  return {
    currency: state.currency,
  };
};

export default connect(mapStateToProps)(ProductCard);
