import React from "react";
import { connect } from "react-redux";
import { changeCurrency } from "./../actions/cartActions";
import { CurrenyContainer } from "./../styles/button";

function ChooseCurrency(props) {
  return (
    <CurrenyContainer>
      <button onClick={() => props.changeCurrency("EUR")}>Euro</button>
      <button onClick={() => props.changeCurrency("USD")}>USD</button>
    </CurrenyContainer>
  );
}

const mapDispatchToProps = (dispatch) => {
  return {
    changeCurrency: (currencyCode) => {
      dispatch(changeCurrency(currencyCode));
    },
  };
};
export default connect(null, mapDispatchToProps)(ChooseCurrency);
