import React, { useState, useEffect } from "react";
import {
  ReviewProductContainer,
  ProductHeader,
} from "./../styles/product-review";
import { Price } from "./../styles/checkout";
import ReviewCart from "./ReviewCart";
import { connect } from "react-redux";
import { changeCurrency } from "../actions/cartActions";

function ReviewCartList(props) {
  const { cart, totalPrice, width } = props;

  const getCurrencySymbol = () => {
    if (props.currency.code === "EUR") {
      return <span>&euro;</span>;
    }
    if (props.currency.code === "USD") {
      return <span>&#36;</span>;
    }
  };

  console.log("Price", totalPrice);
  const handleNumberDecimal = (number) => {
    let numberTwoDecimal = Math.round(number * 100) / 100;

    let numberDecimalSplit = numberTwoDecimal.toString().split(".");
    if (numberDecimalSplit.length === 1) {
      numberDecimalSplit.push("00");
      return numberDecimalSplit.join(".");
    }
    if (numberDecimalSplit[1].length === 1) {
      let oneDigitDecimal = numberDecimalSplit[1];
      numberDecimalSplit.pop();
      numberDecimalSplit.push(`${oneDigitDecimal}0`);
      return numberDecimalSplit.join(".");
    } else {
      return numberTwoDecimal;
    }
  };

  console.log(cart);
  return (
    <ReviewProductContainer width={width}>
      <ProductHeader>
        <p>Item</p>
        <p>Title</p>
        <p>Quantity</p>
        <p>Price</p>
        <p>Total</p>
      </ProductHeader>
      {cart &&
        cart.map((item) => {
          return (
            <ReviewCart
              handleNumberDecimal={handleNumberDecimal}
              product={item.product}
              quantity={item.quantity}
              getCurrencySymbol={getCurrencySymbol}
            />
          );
        })}
      <Price>
        <div className="totals">
          <div>
            <h5>Sub Total:</h5>{" "}
            {totalPrice && (
              <p>
                {getCurrencySymbol()}
                {handleNumberDecimal(props.currency.price * (totalPrice - 10))}
              </p>
            )}
          </div>
          <div>
            <h5>Shipping:</h5>{" "}
            <p>
              {getCurrencySymbol()}
              {handleNumberDecimal(props.currency.price * 10.0)}
            </p>
          </div>
          <div className="final-total">
            <h5>Total:</h5>
            {totalPrice && (
              <p>
                {getCurrencySymbol()}
                {handleNumberDecimal(props.currency.price * totalPrice)}
              </p>
            )}
          </div>
        </div>
      </Price>
    </ReviewProductContainer>
  );
}
const mapStateToProps = (state) => {
  return {
    currency: state.currency,
  };
};

export default connect(mapStateToProps)(ReviewCartList);
